                <van-col span="21"><van-search placeholder="请输入搜索的商品" shape="round" background="#ffffff" v-model = "keywords" @input="history"/></van-col>
                <!-- <van-col span="5"><van-tag type="danger" @click="search">搜索</van-tag></van-col> -->
