                    "毛巾",
                    "电话交换机",
                ],
                keywords:"",
                oldlist:[]
            }
        },
        methods:{
            search(keywords){
                 return this.lists.filter(item => item.includes(keywords))
            },
            history(){
                let text = this.keywords
                this.oldlist.push(text)
            }
            
        },
    
    }
</script>

